import {FC, useState} from 'react';
import {useAppDispatch, useAppSelector} from "../../bll/store";
import {addItem} from "../../bll/slises/cart/cartSlice";
import { Link } from 'react-router-dom';
import {selectCartItemById} from "../../bll/slises/cart/selectors";
import {CartItemType} from "../../bll/slises/cart/types";


export type PizzaBlockType = {
    id: number,
    imageUrl: string,
    title: string,
    types: number[],
    sizes: number[],
    price: number,
    category: number,
    rating: number
}

export const PizzaBlock: FC<PizzaBlockType> = ({title, price, imageUrl, sizes, types, id}) => {

    const typesNames = ['Тонкое', 'Традиционное']
    const [activeType, setActiveType] = useState(0)
    const [activeSize, setActiveSize] = useState(0)
    const dispatch = useAppDispatch();

    // const {count} = useAppSelector<any>(state => state.cart.items.find(obj => obj.id === id))
    const cartItem = useAppSelector(selectCartItemById(id))
    const addedCount = cartItem ? cartItem.count : ''

    const onClickAdd = () => {
        const item: CartItemType = {
            id, title,
            price, imageUrl,
            type: typesNames[activeType], size: sizes[activeSize], count: 1
        }
        dispatch(addItem(item))
    }

    return (
        <div className="pizza-block-wrapper">
            <div className="pizza-block" >
                <Link to={`/pizza/${id}`}>
                    <img
                        className="pizza-block__image"
                        src={imageUrl}
                        alt="Pizza"
                    />
                </Link>
                <h4 className="pizza-block__title">{title}</h4>
                <div className="pizza-block__selector">
                    <ul>
                        {types.map((type, index) => <li key={index} className={activeType === index ? "active" : ''}
                                                        onClick={() => setActiveType(index)}>{typesNames[type]}
                        </li>)}
                    </ul>
                    <ul>
                        {sizes.map((size, index) => <li key={index} className={activeSize === index ? "active" : ''}
                                                        onClick={() => setActiveSize(index)}>{size} см.
                        </li>)}
                    </ul>
                </div>
                <div className="pizza-block__bottom">
                    <div className="pizza-block__price">от {price} ₽</div>
                    <button onClick={onClickAdd} className="button button--outline button--add">
                        <svg
                            width="12"
                            height="12"
                            viewBox="0 0 12 12"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                                fill="white"
                            />
                        </svg>
                        <span>Добавить</span>
                        {addedCount && <i>{addedCount}</i>}
                    </button>
                </div>
            </div>
        </div>
    );
};

